  background-color:darkblue;
}

.bar.learning::before{
  width: calc(80% - 10px);
}

.bar.basic::before{
  width: calc(70% - 10px);
}

.bar.intermediate::before{
  width: calc(60% - 10px);
}

.bar.advanced::before{
  width: calc(80% - 10px);
}

.bar.expert::before{
  width: calc(90% - 10px);
